#toast {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 120px;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  
  width: 200px;
  height: 45px;
  border-radius: 45px;
  background-color: rgba(0,0,0,.5);
  -webkit-transition: opacity .4s ease;
  -o-transition: opacity .4s ease;
  transition: opacity .4s ease;
  z-index: 1000;
}

#toast.active {
  -webkit-animation: fade-out 3s linear .3s;
          animation: fade-out 3s linear .3s;
}

@-webkit-keyframes fade-out {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fade-out {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

